'use strict';

// MODULES //

import isAlmostEqualF64 from '@stdlib/number-float64-base-assert-is-almost-equal';
import isAlmostEqualF32 from '@stdlib/number-float32-base-assert-is-almost-equal';
import reim from '@stdlib/complex-float64-reim';
import reimf from '@stdlib/complex-float32-reim';


// MAIN //

/**
* Tests whether two complex numbers are approximately equal within a specified number of ULPs (units in the last place).
*
* ## Notes
*
* -   The function returns `false` if either input value has a `NaN` real or imaginary component.
* -   The function does not distinguish between `-0` and `+0`, treating them as equal.
*
* @param {ComplexLike} z1 - first complex number
* @param {ComplexLike} z2 - second complex number
* @param {number} maxULP - maximum allowed ULP difference
* @returns {boolean} boolean indicating whether two complex numbers are approximately equal within a specified number of ULPs
*
* @example
* import EPS from '@stdlib/constants-float64-eps';
* import Complex128 from '@stdlib/complex-float64-ctor';
*
* var z1 = new Complex128( 1.0, 3.0 );
* var z2 = new Complex128( 1.0+EPS, 3.0 );
*
* var bool = isAlmostEqual( z1, z2, 0 );
* // returns false
*
* bool = isAlmostEqual( z1, z2, 1 );
* // returns true
*/
function isAlmostEqual( z1, z2, maxULP ) {
	var parts1;
	var parts2;
	var nb1;
	var nb2;
	var f;

	// Complex-like values will not, in general, have these properties, but complex instances will, thus allowing us to distinguish between complex number data types without expensive constructor checks, including when given cross-realm complex number instances:
	nb1 = z1.BYTES_PER_ELEMENT;
	nb2 = z2.BYTES_PER_ELEMENT;

	// Case: isAlmostEqual( complex64, complex64 )
	if ( nb1 === 4 && nb2 === 4 ) {
		f = isAlmostEqualF32;
		parts1 = reimf( z1 );
		parts2 = reimf( z2 );
	}
	// Case: isAlmostEqual( complexLike, complexLike )
	else {
		f = isAlmostEqualF64;
		parts1 = reim( z1 );
		parts2 = reim( z2 );
	}
	return (
		f( parts1[ 0 ], parts2[ 0 ], maxULP ) &&
		f( parts1[ 1 ], parts2[ 1 ], maxULP )
	);
}


// EXPORTS //

export default isAlmostEqual;
